.banner {
  width: 100%;
  height: 99vh;
  padding: 10px 2%;
  background-image: url("../../public/images/banner_main.jpg");
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  overflow: hidden;
}

@media (max-width: 780px) {
  .banner {
    background-size: cover;
  }
}